.roundedBox {
    border-radius: 3px;
    background-color: white;
    padding: 20px;
    margin: 10px;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.2);
}
.expandedBox {
    border-radius: 3px;
    background-color: white;
    padding: 20px;
    margin: 10px;
    box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.2);
    height: auto;
}